* {
  position: relative;
}
body {
  width: 100vw;
  height: 100vh;
  background: black;
}
.wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
}
.num {
  width: 100px;
  height: 200px;
  margin-left: 50px;
  filter: drop-shadow(0 0 15px rgba(255, 255, 255, 1));
}
.dots {
  width: 50px;
  height: 200px;
  margin-left: 50px;
}
.dots > div {
  background: white;
  width: 10px;
  height: 10px;
  position: absolute;
  transform: translate(-50%, -50%);
  &:first-child {
    top: 25%;
    left: 50%;
  }
  &:last-child {
    top: 75%;
    left: 50%;
  }
}

.num > div {
  position: absolute;
  background: white;
}
.num > div.a {
  width: 100px;
  height: 10px;
  clip-path: polygon(0% 50%, 5% 0%, 95% 0%, 100% 50%, 95% 100%, 5% 100%);
}
.num > div.b {
  width: 10px;
  height: 100px;
  clip-path: polygon(50% 0%, 100% 5%, 100% 95%, 50% 100%, 0% 95%, 0% 5%);
}

.top {
  top: -3%;
}
.top-left {
  top: 0;
  left: -8%;
}
.top-right {
  top: 0;
  right: -8%;
}
.middle {
  top: 50%;
  transform: translateY(-50%);
}
.bottom-left {
  bottom: 0;
  left: -8%;
}
.bottom-right {
  bottom: 0;
  right: -8%;
}
.bottom {
  bottom: -3%;
}

.num1 {
  .top,
  .top-left,
  .middle,
  .bottom-left,
  .bottom {
    visibility: hidden;
  }
}
.num2 {
  .top-left,
  .bottom-right {
    visibility: hidden;
  }
}
.num3 {
  .top-left,
  .bottom-left {
    visibility: hidden;
  }
}
.num4 {
  .top,
  .bottom-left,
  .bottom-left,
  .bottom {
    visibility: hidden;
  }
}
.num5 {
  .top-right,
  .bottom-left {
    visibility: hidden;
  }
}
.num6 {
  .top-right {
    visibility: hidden;
  }
}
.num7 {
  .top-left,
  .middle,
  .bottom-left,
  .bottom {
    visibility: hidden;
  }
}
.num8 {
}
.num9 {
  .bottom-left {
    visibility: hidden;
  }
}
.num0 {
  .middle {
    visibility: hidden;
  }
}
